.header {
    display: flex;
    justify-content: space-between;
    max-width: 880px;
    margin: auto;
    padding: 45px 0 37px 0;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 767px) {
    .header {
        margin: 0;
        padding: 28px 27px 28px;
    }
}